label {
display: block;
}

input, select, textarea {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type=submit] {
  width: 100%;
  background-color: #153943;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type=submit]:hover {
  background-color: #fefdfd;
  color: #153943;
  border: 2px solid;
  border-color: #153943;
}

.FormContainer {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}